export function initInputMode() {
   const html = document.documentElement;
   let lockedByKeyboard = false;

   function setInputMode(type) {
      if (html.dataset.input !== type) {
         html.dataset.input = type;
      }
   }

   // ‚úÖ –ù–∞ —Å—Ç–∞—Ä—Ç—ñ –∑–∞–≤–∂–¥–∏ touch
   setInputMode('touch');

   // Keyboard
   window.addEventListener('keydown', (e) => {
      if (e.key === 'Tab') {
         lockedByKeyboard = true;
         setInputMode('keyboard');
      }
   });

   // Mouse —Ä—É—Ö ‚Äî —î–¥–∏–Ω–∏–π —Å–ø–æ—Å—ñ–± —É–≤—ñ–º–∫–Ω—É—Ç–∏ hover
   window.addEventListener(
      'pointermove',
      (e) => {
         if (e.pointerType === 'mouse' && !lockedByKeyboard) {
            setInputMode('mouse');
         }
      },
      { passive: true }
   );

   // Pointer down
   window.addEventListener('pointerdown', (e) => {
      lockedByKeyboard = false;

      if (e.pointerType === 'mouse') {
         setInputMode('mouse');
      } else {
         // ‚ùóÔ∏ètouch / pen –ù–Ü–ö–û–õ–ò –Ω–µ –≤–º–∏–∫–∞—é—Ç—å hover
         setInputMode('touch');
      }
   });
}

