"use strict"

// ===========================================================================================
// -----------------------------
// –õ–û–ì–Ü–ö–ê –í–ò–ü–ê–î–ê–Æ–ß–ò–• –ú–ï–ù–Æ (Dropdowns)
// -----------------------------
export function initDropdowns() {
   const html = document.documentElement;
   const menuItems = document.querySelectorAll('.menu-dropdown__item--submenu');

   const closeAllMenus = () => {
      menuItems.forEach((item) => item.classList.remove('is-active'));
   };

   menuItems.forEach((item) => {
      const link = item.querySelector('.menu-dropdown__link');

      // 1. –û–±—Ä–æ–±–∫–∞ –∫–ª—ñ–∫—É (Touch)
      item.addEventListener('click', (e) => {
         if (html.dataset.input === 'touch' || html.dataset.input === 'pen') {
            if (e.target.closest('.menu-dropdown__link')) {
               e.preventDefault();
               menuItems.forEach((el) => {
                  if (el !== item) el.classList.remove('is-active');
               });
               item.classList.toggle('is-active');
            }
         }
      });

      // 2. –î–û–î–ê–ù–û: –û–±—Ä–æ–±–∫–∞ —Ñ–æ–∫—É—Å—É (Tab)
      // –ö–æ–ª–∏ –ø–æ—Å–∏–ª–∞–Ω–Ω—è –≤—Å–µ—Ä–µ–¥–∏–Ω—ñ item –æ—Ç—Ä–∏–º—É—î —Ñ–æ–∫—É—Å ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ –º–µ–Ω—é
      item.addEventListener('focusin', () => {
         if (html.dataset.input === 'keyboard') {
            item.classList.add('is-active');
         }
      });

      // 3. –û–±—Ä–æ–±–∫–∞ –≤–∏—Ö–æ–¥—É —Ñ–æ–∫—É—Å—É (Tab –≤–∏—Ö–æ–¥–∏—Ç—å –∑–∞ –º–µ–∂—ñ)
      item.addEventListener('focusout', (e) => {
         // –Ø–∫—â–æ –Ω–æ–≤–∏–π –µ–ª–µ–º–µ–Ω—Ç —Ñ–æ–∫—É—Å—É (relatedTarget) –ù–ï –≤—Å–µ—Ä–µ–¥–∏–Ω—ñ –ø–æ—Ç–æ—á–Ω–æ–≥–æ item ‚Äî –∑–∞–∫—Ä–∏–≤–∞—î–º–æ
         if (!item.contains(e.relatedTarget)) {
            item.classList.remove('is-active');
         }
      });
   });

   // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø—Ä–∏ –∫–ª—ñ–∫—É –ø–æ–∑–∞ –º–µ–Ω—é
   document.addEventListener('click', (e) => {
      if (!e.target.closest('.menu-dropdown__item--submenu')) {
         closeAllMenus();
      }
   });

   // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø—Ä–∏ Escape
   document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape') {
         closeAllMenus();
         if (html.dataset.input === 'keyboard') {
            document.activeElement.blur();
         }
      }
   });
}
